"use client";

import { useState } from "react";
import axios from "axios";
import { useRouter } from "next/navigation"; // Changed from 'next/router' for App Router
import Navbar from "@/components/Navbar";
import Footer from "@/components/Footer";
import {
  User,
  Building2,
  Mail,
  Lock,
  Phone,
  Calendar,
  CreditCard,
  Train,
  Hash,
  MapPin,
  ChevronDown, // Added for select dropdown arrow
} from "lucide-react";

export default function Signup() {
  const [role, setRole] = useState("USER");
  const [formData, setFormData] = useState({
    name: "",
    email: "",
    password: "",
    phone_no: "",
    dob: "",
    aadhar_card: "",
    provider_type: "TRAIN_SERVICE",
    contact_number: "",
    gst_number: "",
    train_name: "",
    train_number: "",
    station_name: "",
  });
  const [step, setStep] = useState(1);
  const [loading, setLoading] = useState(false);

  const router = useRouter();

  const handleChange = (e) => {
    setFormData({ ...formData, [e.target.name]: e.target.value });
  };

  const handleRoleChange = (newRole) => {
    setRole(newRole);
    // Reset step if switching to USER or back to step 1 for PROVIDER
    setStep(1);
    // Optionally, clear form data or specific fields when role changes
    // setFormData({ ...initialFormData, provider_type: "TRAIN_SERVICE" });
  };

  const handleNextStep = (e) => {
    e.preventDefault();
    // Add validation for step 1 fields if needed before proceeding
    setStep(2);
  };

  const handlePrevStep = () => {
    setStep(1);
  };

  const handleSignup = async (e) => {
    e.preventDefault();
    setLoading(true);
    try {
      const endpoint =
        role === "USER"
          ? `${process.env.NEXT_PUBLIC_BACKEND_BASE_URL}/auth/user/signup`
          : `${process.env.NEXT_PUBLIC_BACKEND_BASE_URL}/auth/provider/signup`;

      const payload =
        role === "USER"
          ? {
              name: formData.name,
              email: formData.email,
              password: formData.password,
              phone_no: formData.phone_no,
              dob: formData.dob,
              aadhar_card: formData.aadhar_card,
            }
          : {
              name: formData.name,
              email: formData.email,
              password: formData.password,
              provider_type: formData.provider_type,
              contact_number: formData.contact_number,
              gst_number: formData.gst_number,
              train_name: formData.provider_type === "TRAIN_SERVICE" ? formData.train_name : undefined,
              train_number: formData.provider_type === "TRAIN_SERVICE" ? formData.train_number : undefined,
              station_name: formData.provider_type === "STATION_VENDOR" ? formData.station_name : undefined,
            };

      const res = await axios.post(endpoint, payload);
      // Assuming token is in res.data.token or similar
      if (res.data.token) {
        localStorage.setItem("token", res.data.token);
      } else if (res.data.data?.token) { // Common alternative
        localStorage.setItem("token", res.data.data.token);
      }
      // Add a success message/toast here
      router.push(role === "USER" ? "/profile/user" : "/profile/provider");
    } catch (err) {
      // Add a more user-friendly error message/toast here
      alert(
        "Signup failed: " + (err.response?.data?.message || err.message || "Unknown error. Please try again.")
      );
    } finally {
      setLoading(false);
    }
  };

  // Common input field styling
  const inputBaseClasses = "w-full text-slate-700 bg-slate-100 border border-slate-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-blue-500 outline-none transition-colors duration-150 ease-in-out text-base placeholder-slate-400";
  const inputPaddingClasses = "pl-12 pr-4 py-3"; // For inputs with icons
  const inputIconWrapperClasses = "absolute inset-y-0 left-0 flex items-center pl-4 pointer-events-none text-slate-400";


  return (
    <div className="min-h-screen bg-gradient-to-br from-blue-50 via-indigo-50 to-slate-50 flex flex-col">
      <Navbar />
      <main className="container mx-auto px-4 py-12 md:py-16 lg:py-20 flex-grow flex items-center justify-center">
        <div className="w-full max-w-3xl">
          {/* Header */}
          <div className="text-center mb-10">
            <h1 className="text-3xl md:text-4xl font-bold text-slate-800 mb-3 tracking-tight">
              Create Your Account
            </h1>
            <p className="text-slate-600 max-w-lg mx-auto text-base md:text-lg leading-relaxed">
              Join our platform to access personalized services and features
              tailored to your needs.
            </p>
          </div>

          {/* Main Card */}
          <div className="bg-white rounded-xl shadow-xl border border-slate-200 overflow-hidden">
            {/* Role Selection */}
            <div className="flex border-b border-slate-200">
              {[{id: "USER", label: "Passenger", desc: "Book tickets, order food, and more", icon: User},
               {id: "PROVIDER", label: "Service Provider", desc: "Offer train or station services", icon: Building2}].map(roleOption => (
                <div
                  key={roleOption.id}
                  className={`group flex-1 cursor-pointer transition-all duration-150 ease-in-out ${
                    role === roleOption.id
                      ? "bg-blue-50 border-b-[3px] border-blue-600"
                      : "bg-white hover:bg-slate-100"
                  }`}
                  onClick={() => handleRoleChange(roleOption.id)}
                >
                  <div className="flex items-center p-5 md:p-6">
                    <div
                      className={`w-11 h-11 md:w-12 md:h-12 rounded-full flex items-center justify-center transition-colors duration-150 ease-in-out ${
                        role === roleOption.id
                          ? "bg-blue-600 text-white"
                          : "bg-slate-200 text-slate-500 group-hover:bg-blue-100 group-hover:text-blue-500"
                      }`}
                    >
                      <roleOption.icon size={20} strokeWidth={role === roleOption.id ? 2.5 : 2} />
                    </div>
                    <div className="ml-4">
                      <h3 className={`font-semibold text-base md:text-lg transition-colors duration-150 ease-in-out ${
                        role === roleOption.id ? "text-blue-700" : "text-slate-800 group-hover:text-slate-900"
                      }`}>
                        {roleOption.label}
                      </h3>
                      <p className={`text-xs md:text-sm leading-relaxed mt-0.5 transition-colors duration-150 ease-in-out ${
                         role === roleOption.id ? "text-blue-600" : "text-slate-500 group-hover:text-slate-600"
                      }`}>
                        {roleOption.desc}
                      </p>
                    </div>
                  </div>
                </div>
              ))}
            </div>

            {/* Form Container */}
            <div className="p-6 sm:p-8 md:p-10">
              {/* Progress Steps */}
              {role === "PROVIDER" && (
                <div className="flex justify-center mb-10">
                  <div className="flex items-center w-full max-w-xs">
                    {[1, 2].map(stepNumber => (
                      <React.Fragment key={stepNumber}>
                        <div
                          className={`w-9 h-9 rounded-full flex items-center justify-center text-sm font-medium transition-colors duration-300 ease-in-out ${
                            step >= stepNumber
                              ? "bg-blue-600 text-white"
                              : "bg-slate-200 text-slate-600"
                          }`}
                        >
                          {stepNumber}
                        </div>
                        {stepNumber < 2 && (
                          <div
                            className={`flex-1 h-1 mx-3 transition-colors duration-300 ease-in-out ${
                              step >= (stepNumber + 1) ? "bg-blue-600" : "bg-slate-200"
                            }`}
                          ></div>
                        )}
                      </React.Fragment>
                    ))}
                  </div>
                </div>
              )}

              <form
                onSubmit={
                  role === "PROVIDER" && step === 1
                    ? handleNextStep
                    : handleSignup
                }
              >
                {/* Step 1: Basic Information */}
                {(role === "USER" || (role === "PROVIDER" && step === 1)) && (
                  <div className="space-y-5">
                    <div className="relative">
                      <div className={inputIconWrapperClasses}> <User size={18} /> </div>
                      <input name="name" type="text" placeholder="Full Name" value={formData.name} onChange={handleChange} className={`${inputBaseClasses} ${inputPaddingClasses}`} required />
                    </div>

                    <div className="relative">
                      <div className={inputIconWrapperClasses}> <Mail size={18} /> </div>
                      <input name="email" type="email" placeholder="Email Address" value={formData.email} onChange={handleChange} className={`${inputBaseClasses} ${inputPaddingClasses}`} required />
                    </div>

                    <div className="relative">
                      <div className={inputIconWrapperClasses}> <Lock size={18} /> </div>
                      <input name="password" type="password" placeholder="Password (min. 6 characters)" value={formData.password} onChange={handleChange} className={`${inputBaseClasses} ${inputPaddingClasses}`} required minLength={6}/>
                    </div>

                    {role === "USER" && (
                      <>
                        <div className="relative">
                          <div className={inputIconWrapperClasses}> <Phone size={18} /> </div>
                          <input name="phone_no" type="tel" placeholder="Phone Number (e.g., 9876543210)" value={formData.phone_no} onChange={handleChange} className={`${inputBaseClasses} ${inputPaddingClasses}`} required pattern="[0-9]{10}" title="Please enter a 10-digit phone number" />
                        </div>

                        <div className="relative">
                          <div className={inputIconWrapperClasses}> <Calendar size={18} /> </div>
                          <input name="dob" type="date" value={formData.dob} onChange={handleChange} className={`${inputBaseClasses} ${inputPaddingClasses} text-slate-700`} required max={new Date().toISOString().split("T")[0]} />
                        </div>

                        <div className="relative">
                          <div className={inputIconWrapperClasses}> <CreditCard size={18} /> </div>
                          <input name="aadhar_card" type="text" placeholder="Aadhar Card Number (12 digits)" value={formData.aadhar_card} onChange={handleChange} className={`${inputBaseClasses} ${inputPaddingClasses}`} required pattern="\d{12}" title="Please enter a 12-digit Aadhar number" />
                        </div>
                      </>
                    )}

                    {role === "PROVIDER" && (
                      <div className="relative">
                        <label htmlFor="provider_type" className="block text-sm font-medium text-slate-700 mb-1.5"> Provider Type </label>
                        <div className="relative">
                           <select id="provider_type" name="provider_type" value={formData.provider_type} onChange={handleChange} className={`${inputBaseClasses} px-4 py-3 appearance-none pr-10`} required >
                            <option value="TRAIN_SERVICE">Train Service</option>
                            <option value="STATION_VENDOR">Station Vendor</option>
                          </select>
                          <div className="absolute inset-y-0 right-0 flex items-center px-3 pointer-events-none text-slate-400">
                            <ChevronDown size={20} />
                          </div>
                        </div>
                      </div>
                    )}
                  </div>
                )}

                {/* Step 2: Provider Details */}
                {role === "PROVIDER" && step === 2 && (
                  <div className="space-y-5">
                    <div className="relative">
                      <div className={inputIconWrapperClasses}> <Phone size={18} /> </div>
                      <input name="contact_number" type="tel" placeholder="Business Contact Number" value={formData.contact_number} onChange={handleChange} className={`${inputBaseClasses} ${inputPaddingClasses}`} required pattern="[0-9]{10}" title="Please enter a 10-digit phone number"/>
                    </div>

                    <div className="relative">
                      <div className={inputIconWrapperClasses}> <Hash size={18} /> </div>
                      <input name="gst_number" type="text" placeholder="GST Number (e.g., 22AAAAA0000A1Z5)" value={formData.gst_number} onChange={handleChange} className={`${inputBaseClasses} ${inputPaddingClasses}`} required pattern="^[0-9]{2}[A-Z]{5}[0-9]{4}[A-Z]{1}[1-9A-Z]{1}Z[0-9A-Z]{1}$" title="Enter a valid GST number"/>
                    </div>

                    {formData.provider_type === "TRAIN_SERVICE" && (
                      <>
                        <div className="relative">
                          <div className={inputIconWrapperClasses}> <Train size={18} /> </div>
                          <input name="train_name" type="text" placeholder="Train Name" value={formData.train_name} onChange={handleChange} className={`${inputBaseClasses} ${inputPaddingClasses}`} required />
                        </div>
                        <div className="relative">
                          <div className={inputIconWrapperClasses}> <Hash size={18} /> </div>
                          <input name="train_number" type="text" placeholder="Train Number (e.g., 12345)" value={formData.train_number} onChange={handleChange} className={`${inputBaseClasses} ${inputPaddingClasses}`} required pattern="\d{5}" title="Enter a 5-digit train number"/>
                        </div>
                      </>
                    )}

                    {formData.provider_type === "STATION_VENDOR" && (
                      <div className="relative">
                        <div className={inputIconWrapperClasses}> <MapPin size={18} /> </div>
                        <input name="station_name" type="text" placeholder="Station Name" value={formData.station_name} onChange={handleChange} className={`${inputBaseClasses} ${inputPaddingClasses}`} required />
                      </div>
                    )}
                  </div>
                )}

                {/* Form Actions */}
                <div className={`mt-8 flex ${ role === "PROVIDER" && step === 2 ? "justify-between items-center" : "justify-center" } gap-x-4 gap-y-3 flex-wrap`} >
                  {role === "PROVIDER" && step === 2 && (
                    <button type="button" onClick={handlePrevStep} className="px-6 py-2.5 border border-slate-300 text-slate-700 font-medium rounded-lg hover:bg-slate-100 focus:outline-none focus-visible:ring-2 focus-visible:ring-offset-2 focus-visible:ring-blue-500 transition-colors text-base" >
                      Back
                    </button>
                  )}

                  <button type="submit" className={`px-7 py-3 bg-blue-600 hover:bg-blue-700 text-white font-semibold rounded-lg focus:outline-none focus-visible:ring-2 focus-visible:ring-offset-2 focus-visible:ring-blue-500 transition-colors flex items-center justify-center shadow-md hover:shadow-lg text-base disabled:opacity-70 disabled:cursor-not-allowed ${ role === "PROVIDER" && step === 2 ? "min-w-[160px]" : "w-full max-w-xs sm:max-w-sm" }`} disabled={loading} >
                    {loading ? (
                      <svg className="animate-spin -ml-1 mr-3 h-5 w-5 text-white" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" >
                        <circle className="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" strokeWidth="4" ></circle>
                        <path className="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z" ></path>
                      </svg>
                    ) : null}
                    {loading ? 'Processing...' : (role === "PROVIDER" && step === 1 ? "Continue" : "Create Account")}
                  </button>
                </div>
              </form>

              {/* Login Link */}
              <div className="text-center mt-8">
                <p className="text-slate-600 text-base">
                  Already have an account?{" "}
                  <a href="/login" className="text-blue-600 font-semibold hover:text-blue-500 hover:underline focus:outline-none focus-visible:ring-1 focus-visible:ring-blue-500 rounded-sm" >
                    Log In
                  </a>
                </p>
              </div>
            </div>
          </div>
        </div>
      </main>
      <Footer />
    </div>
  );
}